}
.rec_vote_cont {
  display: flex;
  margin: 20px 0px 10px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.record_vote {
  font-weight: 500;
  font-size: 14px;
  color: #fff;
  padding: 12px 20px;
  border-radius: 8px;
  text-align: center;
  letter-spacing: 0.03rem;
  background: var(--main-col);
}

.button:hover, .button:active{
  background: #1736a7;
}
.button:active {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}

.vote_now_list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.vote_now_list li {
  margin: 10px 0px;
  display: flex;
  padding-left: 5px;
  flex-direction: row;
  align-items: center;

  width: 100%;
}
.vote_img_cont {
  width: 16px;
  height: 16px;
  border-radius: 100%;
  overflow: hidden;
  display: flex;
  font-size: 12px;
  align-items: center;
  justify-content: center;
  border: 1px solid #aaa;
}
.vote_img_cont img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%;
}
.vote_now_list li p {
  flex: 1 1;
  font-size: 12px;
  color: var(--wht);
  opacity: 0.7;
  line-height: 18px;
  letter-spacing: 0.03rem;
  font-weight: 300;
}
.light_theme .vote_now_list li p {
  font-weight: 400;
  opacity: 1;
  font-weight: 500;
}

.vote_now_list input[type="radio"] {
  margin: 0;
  width: 14px;
  height: 14px;
  display: grid;
  cursor: pointer;
  appearance: none;
  margin-right: 10px;
  border-radius: 100%;
  place-content: center;
  -webkit-appearance: none;
  background-color: transparent;
  border: 2px solid var(--bord-dark);
}
.vote_now_list input[type="radio"]::before {
  content: "";
  width: 6px;
  height: 6px;
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  background-color: var(--main-col);
  transition: 120ms -webkit-transform ease-in-out;
  transition: 120ms transform ease-in-out;
  transition: 120ms transform ease-in-out, 120ms -webkit-transform ease-in-out;
}
.vote_now_list input[type="radio"]:checked::before {
  -webkit-transform: scale(1);
          transform: scale(1);
}



